button,
textarea,
input,
select,
img,
div,
a{
 -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
 -webkit-tap-highlight-color: transparent;
 -webkit-user-select: none;
 -khtml-user-select: none;
 -moz-user-select: none;
 -ms-user-select: none;
  user-select: none;
}
body{
  --bg-grey: #f1f1f1;
  --grey: #eeeeee;
  --greys: #1d1c1a;
  --white: #ffffff;
  --txt-black: #000000;
  --t-black: rgba(0,0,0,0.9);
  --s-toggle: brightness(0) saturate(100%) invert(96%) sepia(73%) saturate(1788%) hue-rotate(318deg) brightness(122%) contrast(85%);
  --s-black: brightness(0) saturate(100%) invert(0%) sepia(47%) saturate(2513%) hue-rotate(256deg) brightness(93%) contrast(99%);
  background-color: var(--white);
  --margin-classifier: 220px;
  --margin-classifiers: -170px;
}
body.dark{
  --bg-grey: #000000;
  --grey: rgba(75,85,99);
  --greys: #aaaaaa;
  --white: #141414;
  --txt-black: #ffffff;
  --t-black: rgba(255,255,255,0.9);
  --s-toggle: brightness(0) saturate(100%) invert(58%) sepia(22%) saturate(1926%) hue-rotate(7deg) brightness(100%) contrast(110%);
  --s-black: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(1%) hue-rotate(154deg) brightness(104%) contrast(101%);
  --margin-classifier: 0px;
  --margin-classifiers: 0px;
}
*{
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}
main{
  background-color: var(--white);
}
.main{
  margin-top: 70px;
  margin-left: 70px;
  margin-right: 70px;
}
nav{
  display: flex;
  margin-top: 70px;
  margin-left: 70px;
  margin-right: 70px;
}
nav > .left, nav > .middle, nav > .right{
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
}
nav > .left > div > h1{
  color: var(--txt-black);
}
nav > .left > div > p{
  color: var(--t-black);
}
nav > .middle{
  margin: auto;
  order: 3;
  margin-left: auto;
  margin-right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
}
nav > .right{
  margin: auto;
  order: 2;
}
nav > .right > a{
  margin: 0 20px;
  text-decoration: none;
  color: var(--t-black);
}
.svg-nav{
  height: 25px;
  filter: var(--s-black);
  opacity: .8;
  cursor: pointer;
}
.svg-toggle{
  height: 20px;
  filter: var(--s-toggle);
  cursor: pointer;
  margin-left: 20px;
}



@media only screen and (max-width: 768px){
  nav{
    display: block;
    margin: 0;
    padding: 20px;
  }
  nav > .left, nav > .middle, nav > .right{
    margin: 0;
    margin-top: 20px;
  }
  main{
  }
  .main{
    margin: 0;
  }
  nav > .right > a{
    margin: 0;
    margin-right: 17px;
  }
}


.home{
  display: flex;
  margin-top: 70px;
}
.home > .left{
  order: 2;
  margin-left: auto;
  margin-right: 0;
  margin-bottom: -70px;
}
.home > .left > p{
  color: var(--t-black);
  margin-top: 5px;
}
.home > .left, .home > .right{
  position: relative;
  width: 49%;
}
.home > .right > div{
  position: absolute;
  left: 0;
  bottom: 30px;
}
.home > .right > div > h1{
  color: var(--txt-black);
  font-size: 2em;
  font-weight: normal;
}
.home > .right > div > h3{
  color: var(--txt-black);
  font-size: 1.4em;
  font-weight: normal;
  margin-top: 5px;
}
.home > .right > div > p{
  margin-top: 30px;
  color: var(--t-black);
}
.home > .right > div > a{
  text-decoration: none;
  background-color: var(--grey);
  color: var(--txt-black);
  display: block;
  padding: 10px 20px;
  border-radius: 5px;
  width: fit-content;
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.book-svg{
  height: 20px;
  margin-right: 10px;
  filter: var(--s-black);
}
.view-svg{
  height: 15px;
  margin-right: 20px;
  filter: brightness(0) saturate(100%) invert(0%) sepia(47%) saturate(2513%) hue-rotate(256deg) brightness(93%) contrast(99%);
}
.hero-img{
  width: 100%;
  border-radius: 20px;
}

@media only screen and (max-width: 768px){
  .home > .right > div > a{
    width: calc(100% - 45px);
  }
  .home{
    display: block;
    width: 85%;
    margin: auto;
    margin-top: 30px;
  }
  .home > .left, .home > .right{
    width: 100%;
    position: static;
    left: auto;
    bottom: auto;
    margin: 0;
  }
  .home > .right > div{
    position: static;
    margin: auto;
    margin-top: 20px;
  }
  .home > .right > div > h1{
    font-size: 1.2em;
  }
  .home > .right > div > h3{
    font-size: 1em;
    margin: 0;
  }
  .home > .right > div > p{
    font-size: .9em;
  }
  .home > .right > div > a{
    font-size: 1.2em;
    margin-bottom: 70px;
  }
}




.projects{
  background-color: var(--bg-grey);
  padding: 50px;
}
.projects > .project-nav{
  display: flex;
  padding: 30px;
}
.projects > .project-nav > .left{
  display: flex;
  justify-content: center;
  align-items: center;
}
.projects > .project-nav > .right{
  margin-left: auto;
  margin-right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.projects > .project-nav > .left > div > h1{
  font-size: 9em;
  color: var(--t-black);
  line-height: .9;
}
.projects > .project-nav > .right > a{
  text-decoration: none;
  color: black;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
  padding: 20px 40px;
  border-radius: 8px;
  font-size: 1.1em;
  font-weight: 600;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}
.project-1{ grid-area: one; height: 500px; }
.project-2{ grid-area: two; height: 400px; }
.project-3{ grid-area: three; height: 300px;}
.project-grid{
  width: 95%;
  margin: auto;
  margin-top: 30px;
  display: grid;
  grid-template-areas: 
    'one one one'
    'two two three';
  gap: 20px;
}
.project-1, .project-2, .project-3{
  background-image: linear-gradient(to right, #1d1c1a , #535754);
  overflow: hidden;
  display: flex;
  position: relative;
  cursor: pointer;
}
.project-1 > p, .project-2 > p, .project-3 > p{
  color: white;
  background-color: black;
  padding: 5px;
  border-radius: 3px;
  height: fit-content;
  width: fit-content;
  position: absolute;
  top: 10px;
  left: 10px;
}
.project-img{
  height: 100%;
  margin-left: auto;
  margin-right: -50px;
  transition: 2s ease-in-out;
}
.project-img:hover{
  scale: 1.3;
}
.grid-3{
  display: grid;
  grid-template-columns: auto auto auto;
  gap: 20px;
  width: 95%;
  margin: auto;
  margin-top: 30px;
}
.grid-3 > .grid-item{
  background-color: white;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  padding: 15px;
}
.grid-3 > .grid-item > h1{
  font-size: 1.2em;
  margin-top: 20px;
  color: black;
}
.grid-3 > .grid-item > p{
  color: black;
  font-size: .9em;
  opacity: .7;
}
.grid-3 > .grid-item > a{
  text-decoration: none;
  color: black;
  font-weight: bold;
  opacity: .9;
  font-size: .9em;
  margin-top: 5px;
}
.grid-3 > .grid-item > a > span{
  transition: .3s ease-in-out;
}
.grid-3 > .grid-item > a:hover > span{
  margin-left: 10px;
}
.grid-3 > .grid-item > .grid-img-bx{
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
  border-radius: 10px;
}
.grid-3 > .grid-item > .grid-img-bx > .grid-img{
  width: 100%;
}

footer{
  background-color: var(--bg-grey);
  border-top: 2px solid var(--white);
  display: flex;
  padding: 30px 5%;
}
footer > .left > p{
  color: var(--t-black);
  font-weight: bolder;
}
footer > .right{
  margin-left: auto;
  margin-right: 0;
}

@media only screen and (max-width: 768px){
  footer{
    display: block;
  }
  footer > .right{
    margin-top: 10px;
  }
  .grid-3{
    gap: 60px;
    width: 70%;
    grid-template-columns: auto;
  }
  .project-grid{
    grid-template-areas: 
      'one'
      'two'
      'three';
  }
  .project-1, .project-2, .project-3{
    height: 200px;
  }
  .projects{
    padding: 0;
  }
  .projects > .project-nav{
    display: block;
  }
  .projects > .project-nav > .left > div > h1{
    font-size: 4em;
    text-align: center;
  }
  .projects > .project-nav > .right > a{
    margin-top: 70px;
    padding: 15px 40px;
    margin-bottom: 50px;
  }
}



.classifier-all{
  margin-top: 70px;
  background-color: var(--bg-grey);
}
.classifier{
  padding: 50px 100px;
  margin-top: var(--margin-classifier);
}
.classifier > h1{
  font-size: 8em;
  color: var(--txt-black);
  margin-top: var(--margin-classifiers);
}
.classifier > h2{
  font-size: 2em;
  color: var(--txt-black);
}
.classifier-flex{
  display: flex;
  margin-top: 70px;
}
.classifier-flex > .left{
  order: 2;
  width: 65%;
  margin-left: auto;
  margin-right: 0;
}
.classifier-flex > .left > p{
  color: var(--t-black);
  font-size: 1.3em;
}
.classifier-flex > .right{
  width: 30%;
}
.classifier-flex > .right > h1{
  color: var(--t-black);
  font-size: 1.3em;
}
.classifier-flex > .right > p{
  color: var(--t-black);
  font-size: 1.2em;
  margin-bottom: 30px;
}
.classifier-flex > .right > p > a{
  font-weight: bold;
  color: var(--t-black);
}
.classifier-flex > .right > a{
  display: block;
  margin-top: 25px;
  margin-left: 20px;
  text-decoration: none;
  color: var(--t-black);
  font-size: 1.2em;
}
.stack{
  background-color: var(--txt-black);
  color: var(--white);
  padding: 5px 10px;
  border-radius: 8px;
  width: fit-content;
  margin-top: 30px;
  font-weight: bold;
  font-size: 1.2em;
}

.grid-5{
  display: grid;
  grid-template-columns: auto auto auto auto auto auto;
  margin-top: 20px;
  gap: 15px;
}
.grid-5 > .grid-item{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  height: 100px;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}
.stack-img{
  width: 70px;
}
.testimonial{
  margin-top: 20px;
  background-color: white;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  padding: 17px;
  border-radius: 8px;
  border: 1px solid rgba(0,0,0,0.03);
}
.testimonial > p{
  opacity: .980;
  font-size: 1em;
  margin-top: 10px;
}
.testimonial-img{
  width: 100%;
}
.testimonial-nav{
  display: flex;
}
.testimonial-nav > .left{
  overflow: hidden;
  height: 50px;
  width: 50px;
  border-radius: 100px;
  margin-right: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.testimonial-nav > .right > div > h1{
  font-size: 1.2em;
}
.testimonial-nav > .right > div > p{
  opacity: .8;
  font-size: .9em;
}
.proj-2{
  margin-top: 30px;
  display: grid;
  grid-template-columns: auto auto;
  gap: 20px;
}
.proj-item{
  background-image: linear-gradient(to right, #1d1c1a , #535754);
  position: relative;
  height: 300px;
  overflow: hidden;
  display: flex;
  cursor: pointer;
}
.proj-item:hover > .proj-img{
  scale: 1.5;
}
.proj-img{
  height: 100%;
  margin-left: auto;
  margin-right: -70px;
  transition: 2s ease-in-out;
}
.proj-item > p{
  position: absolute;
  top: 30px;
  left: 30px;
  background-color: black;
  width: fit-content;
  height: fit-content;
  color: white;
  padding: 5px;
  border-radius: 5px;
}
.experience-div{
  border: 1px solid red;
  width: 50%;
  margin: auto;
  padding: 15px;
  border: 1px solid rgba(0,0,0,0.1);
  border-radius: 10px;
  background-color: white;
}
.experience-div > .exp-div > h1{
  font-size: 1.4em;
}
.experience-div > .exp-div > h6{
  font-size: .9em;
  font-weight: normal;
  opacity: .5;
}
.experience-div > .exp-div > p{
  margin-top: 10px;
  font-size: 1em;
}
.circle{
  margin: auto;
  border-radius: 50px;
  margin-top: 10px;
  margin-bottom: -24px;
  height: 20px;
  width: 20px;
  background-color: var(--greys);
  animation: scale3 1s infinite;
  transition: all .2s ease-in-out;
}
@keyframes scale3 {
  0% {transform: scale(1);
    box-shadow:0 25px 25px rgba(0, 0, 0, 0.4);
  }
  50% {transform: scale(1.2);
    box-shadow:0 25px 25px rgba(0, 0, 0, 0.6)
  }
   100% {transform: scale(1);
    box-shadow:0 25px 25px rgba(0, 0, 0, 0.4)
  }
}
.line{
  height: 80px;
  border: 2px solid #aaaaaa;
  width: 0;
  margin: auto;
}
.classifier > h4{
  font-size: 3em;
  position: absolute;
  left: 25%;
  margin-top: -55px;
  opacity: .2;
  color: #aaaaaa;
}
.contact-text{
  width: 80%;
  margin: auto;
  margin-top: 30px;
}
.contact-text > h1{
  font-size: 1.5em;
  color: var(--t-black);
}
.contact-text > p{
  font-size: 1em;
  color: var(--t-black);
}
.contact-text > p > a{
  font-weight: bolder;
  color: var(--t-black);
}
h5{
  text-align: center;
  margin-top: 150px;
  font-size: 2em;
  font-weight: 600;
}

@media only screen and (max-width: 768px){
  .contact-text{
    width: 90%;
  }
  .contact-text > h1, .contact-text > p{
    text-align: center;
  }
  .experience-div{
    width: 90%;
  }
  .classifier > h4{
    left: 5%;
  }
  .proj-2{
    grid-template-columns: auto;
  }
  .proj-item{
    height: 200px;
  }
  .grid-5{
    grid-template-columns: auto auto;
  }
  .classifier-flex{
    display: block;
  }
  .classifier-flex > .left, .classifier-flex > .right{
    width: 100%;
    margin: 0;
  }
  .classifier-all{
    margin-top: 30px;
  }
  .classifier{
    padding: 80px 20px;
  }
  .classifier > h1{
    text-align: center;
    font-size: 3em;
  }
  .classifier > h2{
    font-size: 1.6em;
    line-height: 2;
    margin-top: 80px;
  }
}